import React from 'react';
const RiskWarning = () => {
  return (
    <div>
      <header>
        <h2>Risk Warning</h2>
      </header>
      <div>
        <p>
          To help you understand the risks involved when investing in shares,
          &nbsp;mini-bonds and funds on Crowdcube, please read the following
          risk summary. &nbsp;Please #investaware and diversify your investments.&nbsp;
        </p>
        <p><strong>The need for diversification when you invest</strong></p>
        <p>
          Diversification involves spreading your money across different types of
          investments with different risks to reduce your overall risk. However,
          it will not lessen all types of risk. &nbsp;Diversification is an essential
          part of investing. Investors should only invest a proportion of their
          available investment funds via Crowdcube and should balance this with safer,
          more liquid investments. &nbsp;
        </p>
        <p><strong><u>Risks when investing in equity or funds</u></strong></p>
        <p>
          Investing in shares (also known as equity) on Crowdcube does not involve
          a regular return on your investment unlike mini-bonds which offer interest
          paid regularly. &nbsp;
        </p>
        <p>
          Investing in a fund may help to diversify your investments and to spread
          the risk but general risks while investing in equity continue to apply.
          Further specific risks are set out on the applicable fund pitch page.
        </p>
        <p>
          Please bear in mind the following particular risks for equity and
          fund&nbsp;investments:
        </p>
        <p><strong>Loss of investment or tax relief</strong></p>
        <p>
          The majority of start-up businesses fail or do not scale as planned
          and therefore investing in these businesses may involve significant risk.
          &nbsp;It is likely that you may lose all, or part, of your investment.
          &nbsp;You should only invest an amount that you are willing to lose and
          should build a diversified portfolio to spread risk and increase the chance
          of an overall return on your investment capital. If a business you invest
          in fails, neither the company &ndash; nor Crowdcube &ndash; will pay you
          back your investment.
        </p>
        <p>
          Tax relief may also be lost due to your personal circumstances or due
          to the activities of a company.
        </p>
        <p><strong>Lack of liquidity</strong></p>
        <p>
          Liquidity is the ease with which you can sell your shares after you
          have purchased them. Buying shares in businesses pitching through
          Crowdcube cannot be sold easily and they are unlikely to be listed on
          a secondary trading market, such as AIM, Plus or the London Stock Exchange.
          &nbsp;Even successful companies rarely list shares on such an exchange.
          In addition, if you purchase B Investment Shares, these are non-voting
          shares and may not be attractive to potential buyers.&nbsp;
        </p>
        <p><strong>Rarity of dividends</strong></p>
        <p>
          Dividends are payments made by a business to its shareholders from
          the company&rsquo;s profits. &nbsp;Most of the companies pitching
          for equity on the Crowdcube website are start-ups or early stage
          companies, and these companies will rarely pay dividends to their
          investors. This means that you are unlikely to see a return on your
          investment until you are able to sell your shares. &nbsp;Profits are
          typically re-invested into the business to fuel growth and build
          shareholder value. Businesses have no obligation to pay shareholder
          dividends.
        </p>
        <p><strong>Dilution</strong></p>
        <p>
          Any investment in shares made through Crowdcube may be subject to
          dilution in the future. Dilution occurs when a company issues more shares.
          Dilution affects every existing shareholder who does not buy any of the
          new shares being issued. As a result an existing shareholder&#39;s
          proportionate shareholding of the company is reduced, or
          &lsquo;diluted&rsquo;-this has an effect on a number of things,
          including voting, dividends and value.
        </p>
        <p>
          Some businesses who pitch for equity investment through Crowdcube
          offer A-Ordinary Shares, which may include pre-emption rights that
          protect an investor from dilution. &nbsp;In this situation the business
          must give shareholders with A-Ordinary Shares the opportunity to buy
          additional shares during a subsequent fundraising round so that they
          can maintain or preserve their shareholding. Please check a pitch, and
          the Articles of the company to see if the shares you are buying will
          have these pre-emption rights. Most companies do not offer pre-emption
          rights for B Investment Shares.
        </p>
        <p><strong><u>Risks when investing in Mini-bonds</u></strong></p>
        <p>
          Mini-bonds are a very different kind of investment to equity and you
          do not own a stake in the business issuing the mini-bond. &nbsp;Instead
          you receive regular interest payments from the issuing company
          (the &ldquo;Issuer&rdquo;) and then return your initial investment back
          at the end of the mini-bonds term (the maturity). &nbsp; Before investing,
          you must read and agree to the Bond Instrument for each mini-bond as these
          contain the exact terms and conditions, including the interest payments
          and final repayment time between investors and the company raising the
          money. &nbsp;It is important to understand that Issuers are solely
          responsible for their financial status and consequently their ability
          to pay interest and return investors&rsquo; capital when the mini-bonds
          mature. Crowdcube does not issue the mini-bonds listed on the Crowdcube
          platform and is not responsible for their performance.&nbsp; Mini-bonds
          represent a high degree of risk and you should be aware of some of the
          specific risks involved in investing in them.
        </p>
        <p><strong>Loss of investment and interest payments</strong></p>
        <p>
          Issuers, like all businesses, are vulnerable to financial difficultly
          and investing in mini-bonds may involve significant risk of default.
          In the event of an Issuer being unable or unwilling to meet payments
          of interest and capital, it is likely that you may lose all, or part,
          of your initial investment and receive no outstanding or future interest
          payments.&nbsp;
        </p>
        <p>
          If a business you invest in fails, neither the company you invest
          in &ndash; nor Crowdcube &ndash; will pay you back you investment.
          You should only invest an amount that you are willing to lose and should
          build a diversified portfolio to spread risk.
        </p>
        <p>
          Mini-bonds are not insured by a third party nor are they protected
          by any governmental authority such as the Financial Services Compensation
          Scheme. This means that if the Issuer becomes insolvent, investors could
          lose some or all of their money.
        </p>
        <p><strong>Lack of liquidity</strong></p>
        <p>
          Liquidity is the ease with which you can sell your investments to a
          third party after you have purchased them. Mini-bonds purchased from
          Issuers pitching through Crowdcube may be transferrable if specified
          in the Bond Instrument, however they will not be listed on any formal
          investment exchange or secondary trading market such as the LSE ORB
          and so it may be difficult or impossible to find a buyer to purchase
          them. &nbsp; Please refer to the individual mini-bond documentation
          for full details of transferability. &nbsp;Investments in mini-bonds
          through Crowdcube should be viewed as a long term and illiquid investment.
        </p>
        <p><strong>Restricted redemption rights</strong></p>
        <p>
          Issuers of the mini-bonds set the terms for redeeming your capital.
          Investors should be aware that they will not be able to redeem their
          initial investment under any circumstances other than those set out in
          the terms and conditions of the documentation of an individual mini-bond,
          meaning their capital will be locked up for the entire term of the mini-bond,
          typically 3-5 years and should therefore be viewed as a long term and
          illiquid investment.
        </p>
        <p><strong>Unsecured investment</strong></p>
        <p>
          Unless otherwise set out in the Bond Instrument, mini-bonds are
          typically an unsecured obligation of the Issuer, meaning there is no
          security over the property or assets of the Issuer supporting the
          repayment of your &nbsp;interest or capital. &nbsp;This means that if
          an Issuer fails, it is unlikely that an investor will have their initial
          investment or outstanding interest payments returned to them because
          there is no security over any remaining assets.&nbsp;
        </p>
        <p><strong>Early Call Risk</strong></p>
        <p>
          The Issuer has the right to repay you your money at any time prior
          to the formal repayment date. Your investment may be materially curtailed
          because of this.
        </p>
        <p><strong>Lower in the pecking order on winding up</strong></p>
        <p>
          If an Issuer falls into financial difficulty and goes out of business,
          other creditors and debt holders with seniority &ndash; including
          fixed charge holders, administrators, employees who are owed wages, banks,
          and secured debtors - will be compensated first. This means it is unlikely
          mini-bond investors, whose unsecured investment sits below all of the
          previously mentioned in the pecking order, will have their initial investment
          or outstanding interest payments returned to them after higher ranked
          creditors are compensated.&nbsp;
        </p>
        <p><strong>Interest rate and inflation risks</strong></p>
        <p>
          Mini-bonds pay interest at a fixed rate rather than by reference to an
          underlying index. Accordingly you should note that a rise in interest
          rates may adversely affect the relative returns that mini-bonds offer.
          Further, inflation may reduce the real value of the returns over time.
        </p>
      </div>
    </div>
  );
};

export default RiskWarning;



